import React from "react";
import { BrowserRouter, Route, Routes, Link } from "react-router-dom";
import styled from "@emotion/styled";
import "./App.css";

import GifsContext from "./GifsContext";
import GifsFilter from "./components/GifsFilter";
import GifsGrid from "./components/GifsGrid";
import GifPage from "./components/GifPage";

const initialState = {
  filter: "",
  gifs: [],
  query: "",
  total: 0,
};

const GifReducer = (state = initialState, action) => {
  switch (action.type) {
    case "SET_FILTER":
      return {
        ...state,
        filter: action.payload,
      };
    case "SET_GIFS":
      return {
        ...state,
        gifs: action.payload,
        total: action.payload.length,
      };
    case "SET_QUERY":
      return {
        ...state,
        query: action.payload,
      };
    default:
      throw new Error("No action");
  }
};

const Container = styled.div`
  margin: auto;
  width: 1000px;
  max-width: 90%;
  padding-top: 1rem;
`;

const Title = styled.h1`
  text-align: center;
`;

const Main = () => {
  return (
    <Container>
      <Title>
        <Link to="/">Allenbot Gifs</Link>
      </Title>
      <GifsFilter />
      <GifsGrid />
    </Container>
  );
};

function App() {
  const [state, dispatch] = React.useReducer(GifReducer, initialState);

  React.useEffect(() => {
    fetch("/allenbot/gifs.json")
      .then((resp) => resp.json())
      .then((data) =>
        dispatch({
          type: "SET_GIFS",
          payload: data,
        })
      );
  }, []);

  React.useEffect(() => {
    const timeOutId = setTimeout(
      () =>
        dispatch({
          type: "SET_FILTER",
          payload: state.query,
        }),
      600
    );
    return () => clearTimeout(timeOutId);
  }, [state.query]);

  if (!state.gifs.length) {
    return <div>Loading data</div>;
  }

  return (
    <BrowserRouter>
      <GifsContext.Provider
        value={{
          state,
          dispatch,
        }}
      >
        <Routes>
          <Route exact path="/" element={<Main />} />
          <Route path="/gif/:id" element={<GifPage />} />
        </Routes>
      </GifsContext.Provider>
    </BrowserRouter>
  );
}

export default App;
